.gallery {
  padding: 0;
}

.gallery__list {
  margin: 0;
  padding: 0 20px;
  list-style-type: none;

  @media (min-width: $tablet-desktop) {
    display: grid;
    grid-template-columns: 1fr 1fr;
    padding: 0 60px;
  }

  @media (min-width: $desktop) {
    grid-template-columns: 1fr 1fr 1fr;
    padding: 0;
  }
}

.gallery__item {
  border-bottom: solid 1px #e5e5e5;

  @media (min-width: $tablet-desktop) {
    display: grid;
    position: relative;
    align-content: start;

    &:nth-child(1) {
      grid-column: 1 / -1;
    }
  }
}

.gallery__picture {
  display: block;
}

.gallery__info {
  @media (min-width: $tablet-desktop) {
    background-color: $white-smoke;
  }

  @media (min-width: $desktop) {
    position: absolute;
    right: 0;
    bottom: 0;
    left: 0;
    color: $white;
    background-color: rgba(0, 0, 0, 0.3);
  }
}

.gallery__title {
  margin: 0;
  text-transform: uppercase;
}

.gallery__author {
  color: $brown-dark;
  margin: 0;
  line-height: 21px;

  @media (min-width: $desktop) {
    color: $white;
  }
}

.gallery__rate {
  display: flex;
  flex-wrap: wrap;
}

.gallery__icon {
  padding-right: 16px;
}

.gallery__number {
  margin: 0;
  line-height: 21px;
}
